import React from "react";

function ReviewCart(props) {
  const styles = {
    reviewItem: {
      display: "flex",
      margin: "0 auto",
    },
    imageContainer: {
      display: "flex",
      width: "60px",
      height: "60px",
      flex: "1",
      margin: "auto 0",
    },
    infoContainer: {
      width: "330px",
      margin: "auto auto",
      flex: "1",
    },
    paragraph: {
      textAlign: "center",
    },
  };

  return (
    <div style={styles.reviewItem}>
      <div style={styles.imageContainer}>
        <img src={props.product.image} />
      </div>

      <div style={styles.infoContainer}>
        <p style={styles.paragraph}>{props.product.name}</p>
      </div>
      <div style={styles.infoContainer}>
        <p style={styles.paragraph}> {props.quantity} </p>
      </div>
      <div style={styles.infoContainer}>
        <p style={styles.paragraph}>
          {" "}
          &euro;
          {props.handleNumberDecimal(props.product.price)}
        </p>
      </div>
      <div style={styles.infoContainer}>
        <p style={styles.paragraph}>
          {" "}
          &euro;
          {props.handleNumberDecimal(props.product.price * props.quantity)}
        </p>
      </div>
    </div>
  );
}

export default ReviewCart;
