import React from "react";
import ReviewList from "./../components/EmailProductReview/ReviewList";
import { Email, Item, A } from "react-html-email";

function EmailTemplate({ name, address, cart }) {
  const calculateTotal = () => {
    if (cart) {
      let total = cart.reduce((total, item) => {
        total += item.product.price * item.quantity;
        return total;
      }, 0);
      total = total + 10;
      // setTotalPrice(total);
      return total;
    }
  };

  const styles = {
    header: {
      borderRadius: "0 0 40% 40%",
      width: "100%",
      height: "150px",
      objectFit: "contain",
      background: `url(
        "https://images.unsplash.com/photo-1584735935682-2f2b69dff9d2?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=2102&q=80"
      )`,
      backgroundSize: "cover",
      color: "white",
      padding: "10px",
    },
    address: {
      border: "1px solid #444",
      background: "white",
      padding: "10px",
      width: "250px",
      borderRadius: "10px",
      boxShadow: "0 4px 8px 0 rgba(0,0,0,0.2)",
      margin: "10px auto",
      textAlign: "center",
    },
    addressTitle: {
      borderBottom: "1px dotted black",
      paddingBottom: "5px",
      textAlign: "center",
    },
    paragraph: {
      textAlign: "center",
    },
    container: {
      backgroundColor: "#eef2f7",
      borderRadius: "0 0 0 50%",
      maxWidth: "70vw",
      minHeight: "100vh",
    },
    body: {
      padding: "20px 10px",
      color: "#444",
    },
  };
  return (
    // <Email title="link">
    <div style={styles.container}>
      <div>
        <div style={styles.header}>
          <h1 style={styles.paragraph}>Thank You {name}</h1>
          <p style={styles.paragraph}>
            Your order will be delivered soon. Thank you for shopping with us!!
          </p>
        </div>
        <div style={styles.address}>
          <p>Address:</p>
          <p style={styles.paragraph}>Building: {address.building}</p>
          <p style={styles.paragraph}>Street: {address.street}</p>
          <p style={styles.paragraph}>Postcode: {address.postcode}</p>
          <p style={styles.paragraph}>Country: {address.country}</p>
        </div>
        <div>
          <ReviewList width="340px" cart={cart} totalPrice={calculateTotal()} />
        </div>
        <p style={styles.paragraph}>
          {" "}
          To see your order, click{" "}
          <a href="https://sports-hub.herokuapp.com/profile">here</a>
        </p>
        <p style={styles.paragraph}>Regards</p>
        <p style={styles.paragraph}>SportsHub Team</p>
      </div>
    </div>
    // </Email>
  );
}

export default EmailTemplate;
