import React, { useState, useEffect } from "react";
import {
  ReviewProductContainer,
  ProductHeader,
} from "./../../styles/product-review";
import { Price } from "./../../styles/checkout";
import ReviewCartEmail from "./ReviewCartEmail";

function ReviewCartList(props) {
  const { cart, totalPrice } = props;

  const handleNumberDecimal = (number) => {
    let numberTwoDecimal = Math.round(number * 100) / 100;

    let numberDecimalSplit = numberTwoDecimal.toString().split(".");
    if (numberDecimalSplit.length === 1) {
      numberDecimalSplit.push("00");
      return numberDecimalSplit.join(".");
    }
    if (numberDecimalSplit[1].length === 1) {
      let oneDigitDecimal = numberDecimalSplit[1];
      numberDecimalSplit.pop();
      numberDecimalSplit.push(`${oneDigitDecimal}0`);
      return numberDecimalSplit.join(".");
    } else {
      return numberTwoDecimal;
    }
  };

  const styles = {
    header: {
      width: "60%",
      borderRadius: "20px",
      padding: "15px",
      margin: "10px auto",
    },

    headerInfo: {
      display: "flex",
      borderBottom: "1px solid grey",
    },
    headerTitle: {
      textAlign: "center",
      fontSize: "0.8rem",
      width: "100%",
    },
    totals: {
      display: "flex",
      margin: "20px 0",
      // justifyContent: "space-around",
      // alignItems: "center",
    },
  };

  return (
    <div style={styles.header}>
      <div style={styles.headerInfo}>
        <p style={styles.headerTitle}>Item</p>
        <p style={styles.headerTitle}>Title</p>
        <p style={styles.headerTitle}>Quantity</p>
        <p style={styles.headerTitle}>Price</p>
        <p style={styles.headerTitle}>Total</p>
      </div>
      {cart &&
        cart.map((item) => {
          return (
            <ReviewCartEmail
              handleNumberDecimal={handleNumberDecimal}
              product={item.product}
              quantity={item.quantity}
            />
          );
        })}
      <Price>
        <div>
          <div style={styles.totals}>
            <h5 style={styles.headerTitle}>Sub Total:</h5>{" "}
            {totalPrice && (
              <p style={styles.headerTitle}>
                &euro;
                {handleNumberDecimal(totalPrice - 10)}
              </p>
            )}
          </div>
          <div style={styles.totals}>
            <h5 style={styles.headerTitle}>Shipping:</h5>{" "}
            <p style={styles.headerTitle}>
              &euro;
              {handleNumberDecimal(10.0)}
            </p>
          </div>
          <div style={styles.totals}>
            <h5 style={styles.headerTitle}>Total:</h5>
            {totalPrice && (
              <p style={styles.headerTitle}>
                &euro;
                {handleNumberDecimal(totalPrice)}
              </p>
            )}
          </div>
        </div>
      </Price>
    </div>
  );
}

export default ReviewCartList;
